import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import * as _ from "lodash";
import { FiltersInterface } from './filters-interface';
import * as $ from 'jquery';
import * as Blazy from 'Blazy';

@Injectable()
export class LocalStorageService {
  source: any;
  data: any;
  animalData: Array<Object>;
  count: number = 0;
  isShowloading: boolean;
  isUseFavorite: boolean = false;

  constructor(public http: Http) {
  }

  //.
  loadData() {
    this.isShowloading = true;
    this.animalData = [];
    let startTime = new Date().getTime();
    let isFirstVisit = window.localStorage.getItem("isFirstVisit");
    //1.web,apilocalStorage.
    if (isFirstVisit == null || isFirstVisit == 'null') {
      //I. &&  localStorage.
      this.http.get('https://animal-source-proxy.herokuapp.com/')
        .subscribe(value => {
          let temp = value.json();
          this.source = temp.result.results;
          window.localStorage.setItem("metadata_modified", JSON.stringify(this.source[0].metadata_modified));
          // let costTime = (new Date().getTime() - startTime) / 1000 + "s";
          // console.log('-----------(api).' + costTime + '-----------');
        },
        error => {
          alert(',,.');
          window.location.reload();
        });
      //II. localStorage.
      this.http.get('https://animal-proxy2.herokuapp.com/')
        .subscribe(value => {
          this.data = value.json();
          //isFirstVisit,,false.
          _.forEach(this.data.result.results, function (value, key) {
            value.isFavorites = false;
            value.isShow = true;
          });
          window.localStorage.setItem("data", JSON.stringify(this.data.result.results));
          this.animalData = JSON.parse(window.localStorage.getItem("data"));
          this.count = this.animalData.length;
          window.localStorage.setItem("isFirstVisit", 'false');
          // let costTime = (new Date().getTime() - startTime) / 1000 + "s";
          // console.log('-----------(api).' + costTime + '-----------');
          setTimeout(() => {var bLazy = new Blazy({ offset: 150 }); this.isShowloading = false; }, 100);
        },
        error => {
          window.localStorage.setItem("isFirstVisit", 'true');
          alert(',,.');
          window.location.reload();
        });
    } else {
      //2.,,,localStorage.
      let startTime = new Date().getTime();
      let metadata_modified = JSON.parse(window.localStorage.getItem("metadata_modified"));
      let localstorageData: any = JSON.parse(window.localStorage.getItem("data"));
      this.http.get('https://animal-source-proxy.herokuapp.com/')
        .subscribe(value => {
          let temp = value.json();
          this.source = temp.result.results;
          if (metadata_modified != this.source[0].metadata_modified) {
            window.localStorage.setItem("metadata_modified", JSON.stringify(this.source[0].metadata_modified));
            //.
            this.http.get('https://animal-proxy2.herokuapp.com/')
              .subscribe(value => {
                this.data = value.json();
                //localStorageAcceptNum(Primary key),isFavoritestruefalse.
                _.forEach(this.data.result.results, function (value, key) {
                  value.isFavorites = false;
                  value.isShow = true;
                  _.forEach(localstorageData, function (value2, key2) {
                    if (value.AcceptNum == value2.AcceptNum) {
                      if (value2.isFavorites == true || value2.isFavorites == 'true') {
                        value.isFavorites = true;
                      }
                    }
                  });
                });
                window.localStorage.setItem("data", JSON.stringify(this.data.result.results));
                this.animalData = JSON.parse(window.localStorage.getItem("data"));
                this.count = this.animalData.length;
                // let costTime = (new Date().getTime() - startTime) / 1000 + "s";
                // console.log('-----------,.' + costTime + '-----------');
                setTimeout(() => {var bLazy = new Blazy({ offset: 150 }); this.isShowloading = false; }, 100);
              },
              error => {
                let clean_time = '1999-09-09 19:26:17';
                window.localStorage.setItem("metadata_modified", JSON.stringify(clean_time));
                alert(',,.');
                window.location.reload();
              });
          } else {
            this.animalData = JSON.parse(window.localStorage.getItem("data"));
            this.count = this.animalData.length;
            // let costTime = (new Date().getTime() - startTime) / 1000 + "s";
            // console.log('-----------,localStorage.' + costTime + '-----------');
            // console.log(this.animalData);
            setTimeout(() => {var bLazy = new Blazy({ offset: 150 }); this.isShowloading = false; }, 100);
          }
        },
        error => {
          let clean_time = '1999-09-09 19:26:17';
          window.localStorage.setItem("metadata_modified", JSON.stringify(clean_time));
          alert(',,.');
          window.location.reload();
        });
    }
  }

  searchData(filters: FiltersInterface) {
    this.isShowloading = true;
    let animals = JSON.parse(window.localStorage.getItem("data"));
    let results: any = _.filter(animals, filters);
    for (var key in animals) {
      var t = JSON.stringify(animals[key]);
      var result = (t.match(filters.key));
      if (result != null) {
        results.push(animals[key]);
      }
    }
    this.animalData = results;
    this.count = this.animalData.length;
    setTimeout(() => {var bLazy = new Blazy({ offset: 150 }); this.isShowloading = false; }, 100);
    // console.log(results);
  }

  setFavorite(item: any) {
    let now_data = JSON.parse(window.localStorage.getItem("data"));
    if (item.isFavorites == true) {
      _.forEach(now_data, function (value, key) {
        if (item.AcceptNum == value.AcceptNum && item.Name == value.Name) {
          value.isFavorites = true;
        }
      });
    }
    if (item.isFavorites == false) {
      _.forEach(now_data, function (value, key) {
        if (item.AcceptNum == value.AcceptNum && item.Name == value.Name) {
          value.isFavorites = false;
        }
      });
    }
    window.localStorage.setItem("data", JSON.stringify(now_data));
    if(this.isUseFavorite==true){
      this.getfavourityData();
    }
  }

  getfavourityData() {
    this.isShowloading = true;
    let animals = JSON.parse(window.localStorage.getItem("data"));
    let results: any = [];
    _.forEach(animals, function (value, key) {
      if (value.isFavorites == true) {
        results.push(value);
      }
    });
    this.animalData = results;
    this.count = this.animalData.length;
    setTimeout(() => {var bLazy = new Blazy({ offset: 150 }); this.isShowloading = false; }, 100);
  }

}



