import { Component, AfterViewInit } from '@angular/core';
import { LocalStorageService } from './local-storage.service';
import { FiltersInterface } from './filters-interface';
import { NgClass } from '@angular/common';
import * as $ from 'jquery';


@Component({
  selector: 'app-root',
  template: require('./app.component.html'),
  styles: [require('./app.component.scss')],
  directives: [NgClass]
})
export class AppComponent implements AfterViewInit {
  static chosenInitialized = false
  filters: FiltersInterface = {};
  isShow: boolean = false;
  searchkey: string;
  constructor(private localstoragesvc: LocalStorageService) {
    // localStorage.clear(); //localStorage, 
    this.getData();
  }

  //
  getData() {
    this.localstoragesvc.isUseFavorite = false;
    this.localstoragesvc.loadData();
    $("html,body").animate({ scrollTop: 0 }, 0);
  }

  // 
  search() {
    this.localstoragesvc.isUseFavorite = false;
    this.localstoragesvc.searchData(this.filters);
    this.searchkey = this.filters.key;
    $("html,body").animate({ scrollTop: 0 }, 0);
  }

  //
  getfavourityData() {
    this.localstoragesvc.isUseFavorite = true;
    this.localstoragesvc.getfavourityData();
    $("html,body").animate({ scrollTop: 0 }, 0);
  }

  Gotop() {
    $("html, body").animate({ scrollTop: 0 }, 600);
  }

  ngAfterViewInit() {
    setTimeout(function () {
      $("html,body").animate({ scrollTop: 0 }, 0);
    }, 2000);
    if (!AppComponent.chosenInitialized) {
      $(window).scroll(function () {
        if ($(this).scrollTop() > 100) {
          $('.scrollup').fadeIn();
        } else {
          $('.scrollup').fadeOut();
        }
      });
      AppComponent.chosenInitialized = true;
    }
  }

}

