import { Component, OnInit, Input } from '@angular/core';
import { LocalStorageService } from '.././local-storage.service';

@Component({
  selector: 'app-content',
  template: require('./content.component.html'),
  styles: [require('./content.component.scss')],
})
export class ContentComponent  {

  static chosenInitialized:boolean = false
  @Input()
  showData: Array<Object>;
  selectData: Array<Object>;
  ImgUrl: any;
  isShowImg: boolean;
  isShowInfo: boolean;
  constructor(private localstoragesvc: LocalStorageService) {
    this.isShowImg = false;
    this.isShowInfo = false;
    this.selectData = [{
      Name: '',
      Sex: '',
      Type: '',
      Build: '',
      Variety: '',
      Age: '',
      Reason: '',
      AcceptNum: '',
      ChipNum: '',
      IsSterilization: '',
      HairType: '',
      Note: '',
      Resettlement: '',
      Phone: '',
      Email: '',
      ChildreAnlong: '',
      AnimalAnlong: '',
      Bodyweight: '',
      ImageName: '',
    }];
  }

  showImg(url) {
    this.isShowImg = true;
    this.ImgUrl = url;
  }
  selectItem(data) {
    this.isShowInfo = true;
    this.selectData = data;
  }

  setFavorite(item: any) {
    this.localstoragesvc.setFavorite(item);
  }

}


